	module.exports = {"data":{"markdownRemark":{"html":"<p>Many sites are hosted at something other than the root of their domain.</p>\n<p>E.g. a Gatsby blog could live at <code>example.com/blog/</code> or a site could be hosted\non GitHub pages at <code>example.github.io/my-gatsby-site/</code></p>\n<p>Each of these sites need a prefix added to all paths on the site. So a link to\n<code>/my-sweet-blog-post/</code> should be rewritten to <code>/blog/my-sweet-blog-post</code>.</p>\n<p>In addition links to various resources (JavaScript, images, CSS) need the same\nprefix added.</p>\n<p>Luckily, for most sites, this work can be offloaded to Gatsby. Using\n<a href=\"/packages/gatsby-link/\">gatsby-link</a> for internal links ensures those links\nwill be prefixed correctly. Gatsby ensures that paths created internally and by\nwebpack are also correctly prefixed.</p>\n<h2 id=\"development\"><a href=\"#development\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Development</h2>\n<p>During development, write paths as if there was no path prefix e.g. for a blog\nhosted at <code>example.com/blog</code>, don’t add <code>/blog</code> to your links. The prefix will\nbe added when you build for deployment.</p>\n<h2 id=\"production-build\"><a href=\"#production-build\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Production build</h2>\n<p>There are two steps for building a site with path prefixes.</p>\n<p>First define the prefix in your site’s <code>gatsby-config.js</code>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>module<span class=\"token punctuation\">.</span>exports <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// Note: it must *not* have a trailing slash.</span>\n  pathPrefix<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`/blog`</span></span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>Then pass <code>--prefix-paths</code> cmd option to Gatsby.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-sh\"><code>gatsby build --prefix-paths</code></pre>\n      </div>\n<p>NOTE: When running the command without the <code>--prefix-paths</code> flag, Gatsby ignores\nyour <code>pathPrefix</code>.</p>","excerpt":"Many sites are hosted at something other than the root of their domain. E.g. a Gatsby blog could live at  example.com/blog/  or a site could…","timeToRead":1,"frontmatter":{"title":"Adding a Path Prefix"},"parent":{"__typename":"File","relativePath":"docs/path-prefix.md"}}},"pathContext":{"slug":"/docs/path-prefix/"}}

